p.loader {
  width: 100px;
  height: 100px;
  position: relative;
}

p.loader:before, p.loader:after {
  content: "";
  border-radius: 50%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: inset 0 0 10px 2px rgba(0, 0, 0, .3);
}

p.loader:after {
  animation: rotate 2s linear infinite;
  box-shadow: inset 0 2px #ff3d00;
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

